export const getRandomIntInclusive = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

export const getRandomImage = () => {
  return `https://picsum.photos/300/200?random=${getRandomIntInclusive(
    1,
    100
  )}`;
};
